<template>
  <form v-on:submit.prevent="onUpdate()" id="account" class="vertical big">
    <div class="flex">
      <div class="input-100">
        <input type="text" name="UserName"  v-model="username" :placeholder="$t('models.user.userName')" >
      </div>
    </div>
    <div class="flex">
      <div>
        <input type="text" name="firstName"  v-model="firstname"  :placeholder="$t('models.user.firstName')">
      </div>
      <div>
        <input type="text" name="lastName" v-model="lastname" :placeholder="$t('models.user.lastName')">
      </div>
    </div>
    <div class="flex">
      <div class="input-100">
        <input type="email" name="email" v-model="email" :placeholder="$t('models.user.email')">
      </div>
    </div>
    <div class="flex">
      <div>
        <input type="checkbox" name="profile-game" v-model="profilgame" >
        <label>{{$t('models.user.profileGame')}}</label>
      </div>
      <div>

        <input type="checkbox" name="profile-dev" v-model="profildev" >
        <label>{{$t('models.user.profileDev')}}</label>
      </div>
    </div>
    <div class="button-wrapper">
      <altai-button type="submit" :disabled="isDisabled">{{$t('components.user.account.button')}}</altai-button>
    </div>
    <div class="links">
      <router-link :to="{ name: 'Login'}">{{$t('components.user.login.alreadyMember')}}</router-link>
    </div>
</form>
</template>
<script>
import _ from 'lodash'
import AltaiButton from '@/components/ui/AltaiButton'
import API from '@/services/API'
export default {
  name: 'account',
  components: {
    AltaiButton
  },
  data: function data () {
    return {
      id: null,
      username: null,
      firstname: null,
      lastname: null,
      email: null,
      profilgame: false,
      profildev: false,
      checked: false,
      ...this.$store.state.user
    }
  },
  computed: {
    isDisabled () {
      return _.isEqual(this.newUser, this.initialUser) // initial user is not updated, but user is
    },
    initialUser () {
      const user = this.$store.state.user
      return {
        id: user.id || null,
        username: user.username || null,
        firstname: user.firstname || null,
        lastname: user.lastname || null,
        email: user.email || null,
        profilgame: user.profilgame || false,
        profildev: user.profildev || false
      }
    },
    newUser () {
      return {
        id: this.id,
        usname: this.username,
        name: this.firstname,
        lname: this.lastname,
        email: this.email,
        casej: this.profilgame,
        casedev: this.profildev
      }
    }
  },
  methods: {
    onUpdate: function (e) {
      API.user.update(this.newUser, this).then((user) => {
        // go back to the chat interface
        this.$router.push({ name: 'Chat' })
      })
    }
  },
  mounted () {
  }
}
</script>
<style lang="scss">
.flex{

  :first-child{
    margin-right: 8px;
  }
  :not(:first-child){
    margin-left: 8px;
  }
  .input-100{
    width: 100%;
    margin-right: inherit;
    input {
      width: 100%;
    }

  }
  input[type=checkbox] {
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;

  }
  input[type=checkbox] {
    -border-radius: 4px;
    height: 30px;
    width: 30px;
    background: linear-gradient(70deg, rgba(#aaaaaa, 0.5), rgba(#999999, 0.5));
    border: 1px solid #ccc;
  }
  input[type="checkbox"]:checked {
    background: linear-gradient(70deg, rgba(#aaaaaa, 0.5), rgba(#999999, 0.5));
    /*margin:0px;*/
    position: relative;
    &:before {
      font-family: FontAwesome;
      content: '\f00c';
      display: block;
      color: grey;
      font-size: 13px;
      position: absolute;
    }
  }
  label {
    position: relative;
    bottom: 28px;
  }
}
</style>



